.range-input-wrapper {
    position: relative;
    /* padding-top: 36px; */
}
.range-input-value-text {
    position: absolute;
    top: 0;
    padding: 8px 10px;
    min-width: 20px;
    border-radius: 3px;
    font-size: 12px;
    line-height: 1;
    -webkit-box-shadow: 0 1px 8px 0 rgba(0,0,0,.2);
    -moz-box-shadow: 0 1px 8px 0 rgba(0,0,0,.2);
    box-shadow: 0 1px 8px 0 rgba(0,0,0,.2);
    background-color: #fff;
    text-align: center;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}
.test-range-input {
    -webkit-appearance: none;
    width: 100%;
    height: 20px;
    cursor: pointer;
    outline: none;
    margin: 0;
    padding: 0;
    background: transparent;
}
input[type=range] {
    width: 100%;
    /* margin: -6.7px 0; */
    background-color: transparent;
    -webkit-appearance: none;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 1.3px;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-webkit-slider-thumb {
    margin-top: 6.5px;
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
    -webkit-appearance: none;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    /* background: #367ebd; */
  }
  input[type=range]::-moz-range-track {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 1.3px;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-moz-range-thumb {
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    background: transparent;
    border-color: transparent;
    border-width: 0 0;
    color: transparent;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-ms-fill-lower {
    /* background: #2a6495; */
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
  }
  input[type=range]::-ms-fill-upper {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
  }
  input[type=range]::-ms-thumb {
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
    margin-top: 0px;
    /*Needed to keep the Edge thumb centred*/
  }
  /*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
  how to remove the virtical space around the range input in IE*/
  @supports (-ms-ime-align:auto) {
    /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
    input[type=range] {
      margin: 0;
      /*Edge starts the margin from the thumb, not the track as other browsers do*/
    }
  }
  
.test-range-input::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: transparent;
}

.test-range-input::-moz-range-thumb {
    -moz-appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: transparent;
}
.test-range-input::-ms-thumb {
    appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: var(--main-colorr);
}
.test-range-input::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}
.test-range-input::-moz-range-track {
    -webkit-appearance: none;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}
.test-range-input::-ms-track {
    -webkit-appearance: none;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}


