@import url(https://fonts.googleapis.com/css2?family=Jost:wght@600&family=Open+Sans:wght@300;600;800&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Open Sans";
  color: #23303d;
  color: var(--dark-color);
}
p {
  font-family: "Open Sans";
}
:root {
  --main-color: #f28f3e;
  --dark-color: #23303d;
  --passive-color: #ccd0d4;
  --not-so-passive-color-but-also-passive: #727c86;
}
button {
  border: none;
  outline: transparent;
  cursor: pointer;
}
.accent {
  color: #f28f3e;
  color: var(--main-color);
}
.medium-font {
  font-size: 14px;
}
.large-font {
  font-size: 36px;
}
.upper {
  text-transform: uppercase;
}
.b-600 {
  font-weight: 600;
}
.bold {
  font-weight: bold;
}
.t-center {
  text-align: center;
}
.el-center {
  display: block;
  margin: 0 auto;
}

.site-footer {
    margin-top: 120px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: solid #f4f5f5 1px;
}
.footer-text {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    color: var(--not-so-passive-color-but-also-passive);
}
.rect-button {
    padding: 12px 62px;
    color: #ffffff;
    background-color: var(--main-color);
    line-height: 24px;
}
.close-template-button {
    display: none;
}
@media screen and (max-width: 1200px) {
    .close-template-button {
        position: absolute;
        display: block;
        width: 28px;
        height: 28px;
        right: 5px;
        top: 5px;
        border-radius: 50%;
        background-color: transparent;
        border: solid #ffffff 1px;
        background-image: url(/page/static/media/cross.90a49471.svg);
        background-size: 90%;
        background-position: center;
        background-repeat: no-repeat;
    }
}
.bordered-button {
    width: 100%;
    padding: 12px;
    background: transparent;
    color: var(--not-so-passive-color-but-also-passive);
    border: solid var(--passive-color) 1px;
    line-height: 24px;
    text-transform: uppercase;
}
.controls-button {
    width: 32px;
    height: 32px;
    padding: 7px;
    background-color: transparent;
    border: solid var(--passive-color) 1px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.line {
    width: 100%;
    background-color: var(--passive-color);
}
.group-wrapper {
    padding: 20px;
    border: solid var(--passive-color) 1px;
}
.file-input-wrapper {
    padding: 25px;
}
.file-info {
    margin-top: 17px;
    color: var(--dark-color);
}
.file-choose-button {
    margin-top: 22px;
    padding: 12px 78px;
}
.progress-bar {
    width: 100%;
    height: 1px;
    background-color: #f4f5f5;
    max-width: 360px;
}
.progress-line {
    width: 0%;
    height: 1px;
    background-color: var(--main-color);
    transition: .3s;
}
.select-element {
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    position: relative;
    padding: 15px 20px;
    border: solid var(--passive-color) 1px;
    color: var(--passive-color);
    background-color: #ffffff;
}
.select-element-label {
    position: relative;
}
.select-element-label::before {
    content: '';
    display: inline-block;
    position: absolute;
    right: 22px;
    top: 50%;
    border: solid var(--not-so-passive-color-but-also-passive);
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 2px;
    transform: translateY(-50%) rotate(45deg);
    z-index: 3;
}
.text-input {
    width: 100%;
    font-family: 'Open Sans';
    padding: 15px 20px;
    border: solid var(--passive-color) 1px;
    box-sizing: border-box;
    color: var(--dark-color);
    outline: transparent;
}
.text-input::-webkit-input-placeholder {
    color: var(--passive-color);
}
.text-input:-ms-input-placeholder {
    color: var(--passive-color);
}
.text-input::placeholder {
    color: var(--passive-color);
}
.titled-input-label {
    display: block;
}
.titled-input-title {
    line-height: 19px;
    margin-bottom: 15px;
    color: var(--not-so-passive-color-but-also-passive);
}
.text-field .ql-editor {
    min-height: 230px;
}
.buttons-container {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.buttons-container button {
    width: 48%;
    padding: 15px 0;
}
.gray-button {
    background-color: var(--passive-color);
    color: var(--not-so-passive-color-but-also-passive);
}
.modal-bg {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #22222222;
    z-index: 4;
}
.modal-bg .modal {
    width: 35%;
    align-self: center;
    background-color: #ffffff;
    padding: 40px;
}
.modal {
  max-height: 100vh;
  overflow-y: auto;
}
@media screen and (max-width: 1200px) {
    .modal-bg .modal {
        padding: 10px 15px;
        width: 90%;
    }
}

.skills-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
}
.level-input-wrapper {
    position: relative;
}
.level-input-wrapper>* {
    position: absolute;
}
.level-input-mark {
    width: 1px;
    background-color: var(--main-color);
    top: 8px;
    bottom: 8px;
}

.range-input-wrapper {
    position: relative;
    height: 50px;
}
.range-input-wrapper * {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.input-mark-container {
    box-sizing: border-box;
    padding: 8px 20%;
    pointer-events: none;
    z-index: 3;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}
.level-input-mark {
    background-color: var(--skill-color);
}
.skill-settings-inputs-wrapper {
    display: flex;
    justify-content: space-between;
}
.skill-settings-inputs-wrapper .titled-input-label {
    width: 46%;
}
.range-input-wrapper {
    position: relative;
    /* padding-top: 36px; */
}
.range-input-value-text {
    position: absolute;
    top: 0;
    padding: 8px 10px;
    min-width: 20px;
    border-radius: 3px;
    font-size: 12px;
    line-height: 1;
    box-shadow: 0 1px 8px 0 rgba(0,0,0,.2);
    background-color: #fff;
    text-align: center;
    transform: translateX(-50%);
}
.test-range-input {
    -webkit-appearance: none;
    width: 100%;
    height: 20px;
    cursor: pointer;
    outline: none;
    margin: 0;
    padding: 0;
    background: transparent;
}
input[type=range] {
    width: 100%;
    /* margin: -6.7px 0; */
    background-color: transparent;
    -webkit-appearance: none;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 1.3px;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-webkit-slider-thumb {
    margin-top: 6.5px;
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
    -webkit-appearance: none;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    /* background: #367ebd; */
  }
  input[type=range]::-moz-range-track {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 1.3px;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-moz-range-thumb {
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    background: transparent;
    border-color: transparent;
    border-width: 0 0;
    color: transparent;
    width: 100%;
    height: 49.4px;
    cursor: pointer;
  }
  input[type=range]::-ms-fill-lower {
    /* background: #2a6495; */
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
  }
  input[type=range]::-ms-fill-upper {
    /* background: #3071a9; */
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
  }
  input[type=range]::-ms-thumb {
    width: 16px;
    height: 36px;
    /* background: #ffffff; */
    /* border: 1px solid #000000; */
    border-radius: 3px;
    cursor: pointer;
    margin-top: 0px;
    /*Needed to keep the Edge thumb centred*/
  }
  /*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
  how to remove the virtical space around the range input in IE*/
  @supports (-ms-ime-align:auto) {
    /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
    input[type=range] {
      margin: 0;
      /*Edge starts the margin from the thumb, not the track as other browsers do*/
    }
  }
  
.test-range-input::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: transparent;
}

.test-range-input::-moz-range-thumb {
    -moz-appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: transparent;
}
.test-range-input::-ms-thumb {
    appearance: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: var(--main-colorr);
}
.test-range-input::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}
.test-range-input::-moz-range-track {
    -webkit-appearance: none;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}
.test-range-input::-ms-track {
    -webkit-appearance: none;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, var(--skill-color) 0%, var(--skill-color) var(--active-width), var(--skill-bg-color) var(--active-width), var(--skill-bg-color) 100%);
}



