.site-header {
    padding: 25px 30px 20px 30px;
}
.page-title {
    line-height: 19px;
}
.promotion-moto {
    display: block;
    margin-top: 10px;
    font-family: Jost;
    line-height: 44px;
}
.slider-item img {
    width: 100%;
}
#root {
    min-height: 100vh;
}
.start-section {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 73px);
    justify-content: space-between;
}
.start-button {
    margin-top: 25px;
}
.slider-wrapper {
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    padding-bottom: 30px;
    background-color: var(--main-color);
    margin-top: 100px;
}
.slider {
    display: flex;
    justify-content: center;
}
.resume-count-text {
    color: #ffffff;
    margin: 20px 0;
    text-transform: uppercase;
}
.resume-slider {
    width: 70%;
}
.slider-element-label {
    display: inline-block;
}
input[type="radio"]:checked+.slider-element-label {
    transform: scale(.92);
}
.slider-item {
    transition: .3s;
    max-width: 250px;
}
.slider-item:nth-last-child() {
    margin-left: unset;
}

.controls-btn {
    width: 64px;
    height: 64px;
    background-color: transparent;
    display: flex;
    justify-content: center;
    border: solid #ffffff 1px;
    border-radius: 50%;
    align-self: center;
    margin: 0 50px;
    z-index: 4;
}

.controls-btn::before {
    content: '';
    display: block;
    width: 24px;
    height: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    align-self: center;
}
.previous-btn::before {
    background-image: url(/page/static/media/left-arrow.9c8e9c95.svg);
}
.next-btn::before {
    background-image: url(/page/static/media/right-arrow.40f4da39.svg);
}
@media screen and (max-width: 1200px) {
    .controls-btn {
        display: none;
    }
    .resume-slider {
        width: 90%;
    }
    .full-slider {
        width: unset !important;
    }
    .slider-element-label {
        width: 100%;
        display: block;
    }
    .slider-item {
        max-width: unset;
    }
}
