.modal-bg {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #22222222;
    z-index: 4;
}
.modal-bg .modal {
    width: 35%;
    align-self: center;
    background-color: #ffffff;
    padding: 40px;
}
.modal {
  max-height: 100vh;
  overflow-y: auto;
}
@media screen and (max-width: 1200px) {
    .modal-bg .modal {
        padding: 10px 15px;
        width: 90%;
    }
}