.creating-section {
    padding-left: 100px;
    /* display: flex;
    justify-content: stretch; */
}
.section-title {
    font-family: 'Jost';
    font-size: 28px;
    line-height: 40px;
    text-transform: uppercase;
}
.progress-text {
    margin-top: 20px;
}
.user-info-progress {
    margin-top: 5px;
    margin-bottom: 15px;
}
.info-block-header {
    font-size: 20px;
    line-height: 27px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 20px;
}
.user-info-sky-line {
    margin-bottom: 12px;
}
.user-info-section {
    /* max-width: 555px; */
    width: 35%;
}

.file-input-wrapper {
    margin-top: 16px;
}

.file-input-wrapper label {
    display: block;
}

.file-input {
    display: none;
}

.file-img-input-label::before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 88px;
    height: 88px;
    border: solid var(--main-color) 2px;
    border-radius: 50%;
    background-image: url(../../../media/svg/upload-icon.svg);
    background-size: 18px;
    background-position: center;
    background-repeat: no-repeat;
}
.contact-input-container {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.inputs-group {
    width: 47.4%;
}
.contact-input-wrapper {
    margin-top: 35px;
}
.contact-input-wrapper:nth-child(1) {
    margin-top: 0;
}
.link-input-wrapper {
    margin-top: 35px;
}
.add-link-btn {
    margin-top: 35px;
}
.about-info-container {
    margin-top: 20px;
    margin-bottom: 35px;
}
.preview-wrapper {
    background-color: #23303d;
    position: absolute;
    right: 0;
    top: 0;
    width: 50%;
    height: 100vh;
}
.template-whitespace {
    margin: 0 auto;
    margin-top: 70px;
    width: 80%;
    height: 800px;
    background-color: #ffffff;
    overflow: hidden;
}

.download-btn {
    position: absolute;
    right: 0;
    bottom: 0;
}

.add-button {
    display: flex;
    align-items: center;
}

.add-button::after {
    content: '';
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-left: 10px;
    background-image: url(../../../media/svg/plus.svg);
    background-repeat: no-repeat;
    background-size: 14px;
    background-position: center;
    border: solid var(--main-color) 1px;
    padding: 4px;
    box-sizing: border-box;
    border-radius: 50%;
}

.date-inputs-wrapper {
    display: flex;
    margin-top: 20px;
}
.month-input-wrapper {
    display: inline-block;
    width: 240px;
    margin-right: 20px;
}
.start-year-input-wrapper {
    display: inline-block;
    width: 100px;
}
.text-field {
    margin-top: 20px;
}

.experience-input-wrapper {
    margin-top: 20px;
}

.experience-list {
    list-style: none;
}

.user-experience-list-item {
    margin-top: 20px;
    position: relative;
    line-height: 15px;
    color: var(--dark-color);
}
.user-experience-list-item:nth-child(1) {
    margin-top: 0;
}
.user-experience-list-item .controls {
    width: 74px;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
}

.company-name {
    margin-top: 5px;
}

.experience-time {
    margin-top: 10px;
}

.experience-description {
    margin-top: 20px;
    margin-bottom: 20px;
}
.user-skills-list {
    overflow-y: hidden;
    overflow-x: auto;
    padding-bottom: 4px;
}

.user-skills-list::-webkit-scrollbar {
    background-color: var(--passive-color);
    height: 4px;
}
.user-skills-list::-webkit-scrollbar-thumb {
    background-color: var(--main-color);
}
.user-skills-list .skills-list-item {
    cursor: pointer;
    display: block;
    white-space: nowrap;
    padding: 6px 15px;
    background-color: var(--main-color);
    color: #ffffff;
    margin-right: 8px;
}

.skill-settings-container .controls {
    width: 74px;
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    justify-content: space-between;
}

.skills-list-wrapper {
    margin-top: 20px;
}

.skills-settings-list {
    display: flex;
    flex-direction: column;
    list-style: none;
}

.skills-settings-list li {
    margin-top: 16px;
}

.skill-settings-container {
    position: relative;
}

.show-template-button {
    display: none;
}
@media screen and (max-width: 1200px) {
    .show-template-button {
        display: block;
        position: fixed;
        right: 30px;
        bottom: 20px;
        width: 50px;
        height: 50px;
        background-color: var(--main-color);
        border-radius: 50%;
        background-image: url(../../../media/svg/eye.svg);
        background-size: 70%;
        background-position: center;
        background-repeat: no-repeat;
        z-index: 11;
    }
    .creating-section {
        padding: unset;
    }
    .user-info-section {
        margin: 0 auto;
        width: 90%;
        padding: unset;
    }
    .preview-wrapper {
        visibility: hidden;
        position: fixed;
        width: unset;
        min-height: 100vh;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .contact-input-container {
        flex-direction: column;
    }
    .inputs-group {
        margin-top: 35px;
        width: 100%;
    }
    .date-inputs-wrapper {
        flex-direction: column;
    }
    .date-inputs-wrapper label {
        width: 100%;
    }
    .shown {
        
        visibility: visible;
        z-index: 10;
    }
}