* {
  margin: 0;
  padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Jost:wght@600&family=Open+Sans:wght@300;600;800&display=swap');
body {
  font-family: "Open Sans";
  color: var(--dark-color);
}
p {
  font-family: "Open Sans";
}
:root {
  --main-color: #f28f3e;
  --dark-color: #23303d;
  --passive-color: #ccd0d4;
  --not-so-passive-color-but-also-passive: #727c86;
}
button {
  border: none;
  outline: transparent;
  cursor: pointer;
}
.accent {
  color: var(--main-color);
}
.medium-font {
  font-size: 14px;
}
.large-font {
  font-size: 36px;
}
.upper {
  text-transform: uppercase;
}
.b-600 {
  font-weight: 600;
}
.bold {
  font-weight: bold;
}
.t-center {
  text-align: center;
}
.el-center {
  display: block;
  margin: 0 auto;
}
