.level-input-wrapper {
    position: relative;
}
.level-input-wrapper>* {
    position: absolute;
}
.level-input-mark {
    width: 1px;
    background-color: var(--main-color);
    top: 8px;
    bottom: 8px;
}

.range-input-wrapper {
    position: relative;
    height: 50px;
}
.range-input-wrapper * {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.input-mark-container {
    box-sizing: border-box;
    padding: 8px 20%;
    pointer-events: none;
    z-index: 3;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}
.level-input-mark {
    background-color: var(--skill-color);
}
.skill-settings-inputs-wrapper {
    display: flex;
    justify-content: space-between;
}
.skill-settings-inputs-wrapper .titled-input-label {
    width: 46%;
}