.select-element {
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    position: relative;
    padding: 15px 20px;
    border: solid var(--passive-color) 1px;
    color: var(--passive-color);
    background-color: #ffffff;
}
.select-element-label {
    position: relative;
}
.select-element-label::before {
    content: '';
    display: inline-block;
    position: absolute;
    right: 22px;
    top: 50%;
    border: solid var(--not-so-passive-color-but-also-passive);
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 2px;
    transform: translateY(-50%) rotate(45deg);
    z-index: 3;
}