.close-template-button {
    display: none;
}
@media screen and (max-width: 1200px) {
    .close-template-button {
        position: absolute;
        display: block;
        width: 28px;
        height: 28px;
        right: 5px;
        top: 5px;
        border-radius: 50%;
        background-color: transparent;
        border: solid #ffffff 1px;
        background-image: url(../../../media/svg/cross.svg);
        background-size: 90%;
        background-position: center;
        background-repeat: no-repeat;
    }
}